class BotApi {
    static queryGET(url) {
        var xhr = new XMLHttpRequest()
        xhr.open('GET', `https://pavlentygood.fvds.ru/query.php?url=${encodeURIComponent(url)}`, false)
        xhr.send()
        return xhr.responseText
    }

    static getUser(id) {
        return this.queryGET(`app_info?userId=${id}`)
    }
}

export default BotApi